<template>
  <div>
    <h1>random selector</h1>
    <div class="page">
      <div @click="pageChangeToR" v-if="flag.left" class="flag-left">
        <svg xmlns="http://www.w3.org/2000/svg" width="22.703" height="21.928">
          <path fill="#37474F" d="M1.056 21.928c0-6.531 5.661-9.034 10.018-9.375V18.1L22.7 9.044 11.073 0v4.836a10.5 10.5 0 0 0-7.344 3.352C-.618 12.946-.008 21 .076 21.928z"/>
        </svg>
      </div>
      <div @click="pageChangeToI" v-if="flag.right" class="flag-right">
        <svg xmlns="http://www.w3.org/2000/svg" width="22.703" height="21.928" style="transform: scaleX(-1);">
          <path fill="#37474F" d="M1.056 21.928c0-6.531 5.661-9.034 10.018-9.375V18.1L22.7 9.044 11.073 0v4.836a10.5 10.5 0 0 0-7.344 3.352C-.618 12.946-.008 21 .076 21.928z"/>
        </svg>
      </div>
    </div>
    
    <div class="main">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <input_data @inputdata-to-app="temp"/>
      <div></div>
      <random_selector :inputList="bus"/>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
    </div>
  </div>
</template>

<script setup>
  import { reactive, ref } from 'vue';
  import input_data from './components/input_data.vue'
  import random_selector from './components/random_selector.vue'

  const bus = ref(null)
  const temp = (val) => {
    bus.value = val
    // console.log(bus.value);
  }
  let flag = reactive({
    left: false,
    right: false,
  })
  const width = screen.width
  if (width <= 767) {
    flag.left = true
  }
  
  const pageChangeToR = () => {
    let i = document.querySelector('.input-data')
    let r = document.querySelector('.random-selector')

    // i.style.transition = 'width .5s cubic-bezier(0.03, 0.02, 0.23, 1) .2s'
    // r.style.transition = 'width .5s cubic-bezier(0.03, 0.02, 0.23, 1) .1s'

    i.style.opacity = 0
    i.style.width = 0
    r.style.opacity = 1
    r.style.width = 'auto'

    // console.log(i.style.opacity , r.style.opacity);
    flag.left = false
    flag.right = true
  }
  const pageChangeToI = () => {
    let i = document.querySelector('.input-data')
    let r = document.querySelector('.random-selector')

    // i.style.transition = 'width .5s cubic-bezier(0.03, 0.02, 0.23, 1) .2s'
    // r.style.transition = 'width .5s cubic-bezier(0.03, 0.02, 0.23, 1) .1s'

    i.style.opacity = 1
    i.style.width = 'auto'
    r.style.opacity = 0
    r.style.width = 0
 
    // console.log(i.style.opacity , r.style.opacity);
    flag.left = true
    flag.right = false
  }
</script>

<style>
  :root {
    --main-color: #E1F5FE;
    --secondary-color: #81C784;
    --text-color: #37474F;
  }
  #app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    margin-top: 60px;
  }
  body {
    background-color: var(--main-color);
    color: var(--text-color);
  }
  .main {
    display: flex;
    position: relative;
    justify-content: space-between;
    margin-top: 4%;
  }
  @media screen and (max-width: 767px) {
    html, body {
      overflow: hidden;
      /* width: 100%;
      height: 100%; */
    }
    .main > div:not(.input-data , .random-selector) {
      display: none;
    }
    .random-selector {
      opacity: 0;
      width: 0;
    }
    .main {
      overflow: hidden;
      justify-content: center;
      margin: 0;
    }
  }


</style>
