<template>
    <div class="input-data">
        <h1>新增選項</h1>
        <input type="text" name="" id="" placeholder="輸入選項" v-model="input.inputData" @keyup.enter="append()">
        <div class="add" @click="append()" title="新增選項">
            <svg xmlns="http://www.w3.org/2000/svg"  viewBox="0 0 29 29" width="24px" height="24px">
                <path fill-rule="evenodd" d="M 11 2 L 11 11 L 2 11 L 2 13 L 11 13 L 11 22 L 13 22 L 13 13 L 22 13 L 22 11 L 13 11 L 13 2 Z"/>
            </svg>
        </div>
        <div class="display-inputData">
            <h3>目前選項：</h3>
            <ul>
                <li v-for="(data, index) in input.inputList" :key="index">
                    <div>{{ data }}</div>
                    <div class="delete" @click="deleteItem(index)" title="刪除選項">
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 29 29" width="24px" height="24px">
                            <path fill-rule="evenodd" d="M 7 7 L 9 5 L 14 10 L 19 5 L 21 7 L 16 12 L 21 17 L 19 19 L 14 14 L 9 19 L 7 17 L 12 12 Z"/>
                        </svg>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script setup>
    import { reactive, defineEmits } from 'vue';

    const input = reactive({
        inputData: '',
        inputList: [],
    })
    const emit = defineEmits(['inputdata-to-app'])
    const append = () => {
        if (input.inputData !== '' & input.inputData.split(' ').join('').length !==0) {
            input.inputList.push(input.inputData)
            input.inputData = ''
            // 新增選項時，傳出input.inputList到父組件App
            emit('inputdata-to-app', input.inputList)
        }
    }
    const deleteItem = (index) => {
        input.inputList.splice(index, 1)
        // 刪除選項時，傳出input.inputList到父組件App
        emit('inputdata-to-app', input.inputList)
    }
    
</script>
<style >
    .input-data {
        /* border: 1px solid; */
        position: relative;
        /* left: 30%; */
    }
    .input-data input {
        width: 200px;
        height: 20px;
        border: 1px solid #b0bec5;
        border-radius: 4px;
    }
    .input-data input[type="text"]:focus {
        border-color: rgb(60, 98, 201);
        outline: none;
    }
    .add {
        display: inline-flex;
        position: relative;
        margin-left: 5px;
        border: 1px solid rgb(60, 98, 201);
        width: 23px;
        height: 23px;
        background-color: rgb(60, 98, 201);
        top: 8px;
        border-radius: 4px;
        cursor: pointer;
    }
    .add svg {
        /* display: inline-flex; */
        position: relative;
        left: 2px;
        top: 2px;
        fill: white;
    }
    .display-inputData ul{
        list-style: none;
        padding: 0px;
        display: inline-flex;
        flex-direction: column;
        align-items: flex-end;
        margin-top: 0px;
    }
    .display-inputData ul li {
        position: relative;
    }
    .display-inputData ul li div {
        position: relative;
        display: inline-flex;
        bottom: 4px;
    }
    .delete {
        display: inline-flex;
        position: relative;
        margin-left: 10px;
        border: 1px solid red;
        width: 22px;
        height: 22px;
        background-color: red;
        top: 3px;
        border-radius: 4px;
        cursor: pointer;
    }
    .delete svg {
        position: relative;
        fill: white;
        /* left: 1px; */
        top: 1px;
    }
</style>