<template>
    <div class="random-selector">
        <h1>隨機抽選</h1>
        <a class="button" @click="start()">START</a>
        <div v-for="(item, index) in inputList" :key="index" v-show="index === inputListLength">{{ item }}</div>
    </div>
</template>
<script setup>
    import { defineProps, ref, toRefs } from 'vue'
    const props = defineProps(['inputList'])
    const { inputList } = toRefs(props)
    let inputListLength = ref(0)

    const start = () => {
        if (inputList.value === null) {
            return
        }
        inputListLength.value = Object.values(inputList.value).length
        const time = setInterval(() => {
            inputListLength.value = (inputListLength.value + 1) % inputList.value.length
        }, 50);
        setTimeout(() => {
            clearInterval(time)
            // console.log((Math.random() * (1500 - 1000 + 1)) + 1000);
        }, Math.floor(Math.random() * (1500 - 1000 + 1)) + 1000);
        //取1~1.5秒之間隨機數當作結束抽選的時間，達成每次抽選結果不一定相同的目的
    }
</script>
<style>
    .random-selector {
        /* border: 1px solid; */
        position: relative;
        /* right: 35%; */
        /* width: 768px; */
    }
    .button{
        position: relative;
        background-color: white;
        border: 1px solid rgb(60, 98, 201);
        border-radius: 4em;
        font-size: 16px;
        color: #37474F;
        padding: 0.8em 1.8em;
        cursor:pointer;
        user-select:none;
        text-align: center;
        text-decoration: none;
        cursor: pointer;
        transition-duration: 0.4s;
        -webkit-transition-duration: 0.4s; /* Safari */
        top: 14px;
    }
    .button:hover {
        transition-duration: 0.1s;
        /* background-color: rgb(120, 141, 211); */
        background-color: rgb(60, 98, 201);
        color: white;
    }
    .button:after {
        content: "";
        display: block;
        position: absolute;
        border-radius: 4em;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        transition: all 0.5s;
        box-shadow: 0 0 10px 40px white;
        
    }
    .button:active:after {
        box-shadow: 0 0 0 0 white;
        position: absolute;
        border-radius: 4em;
        left: 0;
        top: 0;
        opacity: 1;
        transition: 0s;
    }
    .button:active {
        top: 14px;
    }
    .button ~ div {
        position: relative;
        top: 80px;
        font-size: 2em;
    }
</style>